.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    /* background-color: white; */
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
    background-image: url('../../../photo-1525362081669-2b476bb628c3.jpeg');
    background-size: cover;
}

@media (min-width:500px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
transform: translateX(0)
}

.Close {
transform: translateX(-100%);
}

.Logo {
    height: 11%;
    margin-bottom: 30px;
}

